// updated 25.09.2025.

@use "sass:color";

%admin-settings {

	//colors
	$color_silver: #cccccc;
	$color_mercury: #e5e5e5;
	$color_white: white;
	$color_primary: #2196f3;
	$color_info: #2196f3;
	$color_success: #8bc34a;
	$color_error: #f44336;
	$color_danger: #ff5722;
	$color_warning: #ffc107;
	$color_black: black;
	$max_content_width: 600px;


	[class^="devnet-plugin-option-notice"],
	.devnet-plugin-panel-description {
		position: relative;
		max-width: 800px;
		padding: 1rem;
		margin: 1rem 0 3rem;
		box-shadow: 0 5px 16px -12px black;
		border-left: 4px solid $color_info;
		background-color: lighten($color: $color_info, $amount: 43);

		&[class$="--warning"],
		&.devnet-plugin-warning {
			border-left: 4px solid $color_warning;
			background-color: lighten($color: $color_warning, $amount: 43);
		}

		&[class$="--alert"],
		&.devnet-plugin-alert {
			border-left: 4px solid $color_error;
			background-color: lighten($color: $color_error, $amount: 37);
		}
	}

	[class^="devnet-plugin-option-notice"] {
		margin: 0;
	}

	.devnet-visible-tooltip {
		width: fit-content;
		padding: 0 0.5rem;
		margin-left: 2px;
		color: $color_white;
		background-color: $color_primary;
		font-size: 0.75rem;
	}

	input,
	textarea,
	select {
		display: block;

		&.disabled {
			pointer-events: none;
			cursor: no-drop;
		}
	}

	input {
		&.disabled {
			pointer-events: none;
			cursor: no-drop;
		}

		&#submit {
			padding: 10px 24px;
			font-size: 1.1rem;
			margin-left: -10px;
			border-bottom: 3px solid $color_black;
			border-radius: 6px;
			transition: all 0.3s ease-in-out;
		}

		&:checked+.devnet-switch {
			background-color: $color_primary;

			&:before {
				transform: translateX(26px);
			}
		}

		&:focus+.devnet-switch {
			box-shadow: 0 0 1px $color_primary;
		}
	}

	input[type='text'],
	input[type='password'],
	input[type='url'],
	select,
	textarea {
		width: 100%;
		max-width: 25rem;
	}

	input[type='number'] {
		width: 7rem;
	}

	input[type='text'],
	input[type='password'],
	input[type='number'],
	input[type='url'],
	input[type='button'],
	select {
		height: 2.5rem !important;
	}

	.dvnt-f-url,
	.dvnt-f-browse {
		display: inline-block;
	}

	.dvnt-f-preview {
		display: block;
	}

	.wp-picker-input-wrap {

		&:not(.hidden) {
			display: inline-flex;
		}

		.wp-color-picker-field {
			height: 1rem !important;
		}

		.wp-picker-clear,
		.wp-picker-default {
			height: 30px !important;
		}
	}

	select {
		padding-right: 2rem;
	}

	.select2 {
		min-width: 300px;

		.select2-selection--single {
			height: 2.7rem;
			padding: 0.5rem;
			padding-right: 2rem;

			.select2-selection__arrow {
				height: inherit;
			}
		}
	}

	input.has-unit-box {
		display: inline-block;
		padding-right: 3rem;
		background: linear-gradient(to left, #e5e5e5 2.35rem, #ffffff 2.35rem)
	}

	.input-unit-box {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 2.35rem;
		margin-left: -2.5rem;
		cursor: default;

		@media screen and (max-width: 782px) {
			//height: 2.25rem;
		}
	}

	tr.has-unit-selector {

		input {
			display: inline-block;
			width: 5rem;
		}

		select {
			display: inline-block;
			height: 2.5rem !important;
			line-height: 1;
			width: 4.5rem;
			vertical-align: top;
		}
	}

	fieldset {
		position: relative;
	}

	.description {
		max-width: $max_content_width;
		font-size: 13px;
	}

	.switch {
		position: relative;
		display: inline-block;
		width: 56px;
		height: 30px;
		margin: 0 !important;

		+span {
			&.description {
				position: absolute;
				max-width: $max_content_width;
				margin-left: 1rem;
				top: 50%;
				transform: translateY(-50%);
			}

			&.desc {
				margin-left: 1rem !important;
			}
		}

		input {
			opacity: 0;
			width: 0;
			height: 0;
		}
	}

	.devnet-switch {
		position: absolute;
		cursor: pointer;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: $color_silver;
		transition: 0.4s;

		&:before {
			position: absolute;
			content: '';
			height: 22px;
			width: 22px;
			left: 4px;
			bottom: 4px;
			background-color: $color_white;
			transition: 0.4s;
		}

		&.devnet-switch--round {
			border-radius: 34px;

			&:before {
				border-radius: 50% !important;
			}
		}
	}

	.radio-images {
		display: flex;
		flex-wrap: wrap;
		max-width: $max_content_width;
		padding: 1rem;
		background-color: white;

		[type='radio'] {
			position: absolute;
			opacity: 0;
			width: 0;
			height: 0;
		}

		[type='radio']+.img {
			cursor: pointer;
			display: block;
			width: 3rem;
			height: 3rem;
			padding: 0.5rem;
		}

		.img svg {
			width: 3rem;
			height: 3rem;
		}

		[type='radio']:checked+.img {
			outline: 2px solid $color_primary;
		}
	}

	tr th label {
		cursor: default;
	}

	tr.disabled,
	tr[class$="__disabled"],
	.devnet-btn.disabled {
		opacity: 0.3;
		pointer-events: none;
		cursor: no-drop;
	}

	.group h2 {
		font-size: 2rem;
		padding-bottom: 2.5rem;
		border-bottom: 1px solid $color_silver;
	}

	.has-tabs {

		&>h2 {
			border: none;
			padding-bottom: 0;
		}

		.group h2 {
			font-size: 1.5rem;
			border: none;
			padding: 1rem 0;
		}
	}

	.dvnt-groups {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;

		.dvnt-group {
			width: 22rem;
			margin-right: 1rem;
			margin-bottom: 1rem;
			padding: 1rem;
			background-color: rgba(255, 255, 255, 0.75);
			border-bottom: 0.1rem solid lightgray;

			.dvnt-group-field {
				margin-bottom: 1.3rem;

				label {
					display: block;
					font-weight: bold;
					margin-bottom: 0.3rem;
				}

				.switch+span.description {
					margin-left: 4rem;
				}
			}

			.dvnt-remove-group {
				float: right;
				color: $color_error;
			}
		}

		button.dvnt-repeat-group {
			width: 2.5rem;
			height: 2.5rem;
			align-self: flex-end;
			margin-top: 1rem;
			font-size: 2rem;
			line-height: 1;
			padding-bottom: 1.25rem;
			color: white;
			background-color: $color_primary;
			border: none;
			outline: none;
			cursor: pointer;
			transition: all .2s ease-out;

			&:hover {
				background-color: scale($color_primary, 15%);
			}
		}
	}

	tr.info {
		td {
			padding: 5rem 0 1rem;

			&:before {
				content: '';
				width: 100%;
				height: 1px;
				background-color: lightgray;
				display: block;
				position: absolute;
				margin-top: -3rem;
			}
		}

		.info-title {
			display: block;
			font-size: 1.1rem;
			font-weight: bold;
			margin-bottom: 0.5rem;
		}

		.info-description {
			display: block;

			input {
				display: inline-block;
				width: max-content;
				height: 1rem !important;
				background-color: $color_mercury;
				border: none;
			}
		}

		&.subinfo td {
			padding: 0 0 1rem;

			&:before {
				content: none;
			}
		}
	}

	.inline+.description {
		display: inline-block;
		margin-left: 0.5rem;
		vertical-align: bottom;
	}

	table.form-table {
		margin-bottom: 3rem !important;
	}

	.devnet-text-change {
		padding: 2px 3px;
		animation: devnet-animation-change 1s ease-in-out 0s;
	}

	@keyframes devnet-animation-change {
		from {
			background: $color_primary;
		}

		to {
			background: transparent;
		}
	}

	.devnet-btn {
		margin: 0;
		padding: 0.5rem;
		border: none;
		outline: none;
		font-size: initial;
		box-shadow: 0 3px 9px -3px black;
		transition: all .2s ease-out;

		&:hover {
			cursor: pointer;
			box-shadow: 0 0 9px -3px black;
		}

		&.devnet-danger-btn {
			background-color: $color_danger;
			color: $color_white;

			&:hover {
				background-color: scale($color_danger, 10%);
			}
		}
	}


	.wp-editor-wrap.html-active {
		.quicktags-toolbar {
			display: flex;
			flex-wrap: wrap;
		}

		textarea {
			max-width: 100%;
		}
	}

}

%admin-settings-rtl {

	.description {
		margin-left: 0;
		margin-right: 0.3rem;
	}

	select {
		padding-right: 1rem;
		padding-left: 2rem;
	}

	input.has-unit-box {
		padding-right: 1rem;
		padding-left: 3rem;
		background: linear-gradient(to right, #e5e5e5 2.35rem, #ffffff 2.35rem)
	}

	.input-unit-box {
		margin-right: -2.5rem;
		position: relative;
	}
}