@import './plugin-settings/settings';


.fsl-wrap {
	padding-top: 1rem;

	@extend %admin-settings;
}

html[dir="rtl"] .fsl-wrap {
	@extend %admin-settings-rtl;
}


#wpfooter {
	display: block !important;
	background-color: white;
}

#footer-upgrade {
	display: none;
}

.fsl-admin-footer {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	.fsl-donation {
		a {
			display: flex;
			justify-content: space-between;
			align-items: center;
			background-color: #95578a;
			color: white;
			padding: 1rem;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}

		.paypal-button {
			padding: 0.1rem 0.5rem;
			background: #ffc438;
			color: #111;
			border-radius: 1rem;

			&:hover {
				background: #dfab32;
			}

			img {
				max-height: 1rem;
			}
		}
	}
}

#devnet_fsl_notice_bar {

	tr.margin_y,
	tr.margin_x {
		.regular-text {
			width: auto;
		}
	}
}

tr {

	&.hide-option-row,
	&.circle_size__disabled,
	&.inside_circle__disabled,
	&.icon__disabled,
	&.icon_color__disabled,
	&.circle_bg_color__disabled {
		display: none !important;
	}

	&[class$="__disabled"] {
		position: relative;

		&:after {
			content: "PRO";
			position: absolute;
			top: 0;
			left: 0;
			background-color: #673ab7;
			color: white;
			padding: 0.1rem 0.3rem;
		}
	}
}

tr.custom_threshold_per_method,
tr.custom_threshold_per_method__disabled {
	.dvnt-group {
		overflow: auto;
		max-height: 50vh;
	}

	.dvnt-group-field {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		&.field-type-info label {
			text-transform: uppercase;
			font-size: 1rem;
			border-bottom: 1px solid #cbcbcb;
			width: 100%;
		}

		&.field-type-number label {
			font-weight: 400 !important;
		}

		.description {
			display: none;
		}
	}
}

tr[class^="shortcode_info"] input.fsl-shortcode {
	width: auto !important;
}